.scoreboard-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2;
}
.scoreboard {
  max-width: 600px;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  margin: 0 auto;
}
.scoreboard__player {
  width: 50%;
  text-align: center;
  padding: 10px;
  margin: 10px;
  font-size: 0;
  height: 40px;
  fill: #251919;
  color: #251919;
  position: relative;

}
body.x-won .scoreboard__player--x{
  -webkit-animation: player-win 1s;
          animation: player-win 1s;
  -webkit-transform-origin: top left;
      -ms-transform-origin: top left;
          transform-origin: top left;
}
body.o-won .scoreboard__player--o{
  -webkit-animation: player-win 1s;
          animation: player-win 1s;
  -webkit-transform-origin: top right;
      -ms-transform-origin: top right;
          transform-origin: top right;
}
.scoreboard__score {
  font-size: 28px;
  font-weight: 600;
  line-height: 20px;
  display: inline-block;
  text-align: center;
  width: 30px;
}
.scoreboard__player--turn {
  fill: #e9851e;
  color: #e9851e;
}

/* Apply Current player */
body.x-current-player .scoreboard__player--x,
body.o-current-player .scoreboard__player--o {
  fill: #e9851e;
  color: #e9851e;
}

/* Background */
body.x-current-player .scoreboard__player--x:before,
body.o-current-player .scoreboard__player--o:before {
  -webkit-animation: take-turn 0.4s;
          animation: take-turn 0.4s;
  content: "";
  background: #251919;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.scoreboard__player svg {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}
@-webkit-keyframes take-turn {
  from { -webkit-transform: scale(0.9); transform: scale(0.9); }
  to { -webkit-transform: scale(1); transform: scale(1); }
}
@keyframes take-turn {
  from { -webkit-transform: scale(0.9); transform: scale(0.9); }
  to { -webkit-transform: scale(1); transform: scale(1); }
}
@-webkit-keyframes player-win {
  0% { -webkit-transform: scale(1); transform: scale(1); }
  50% { -webkit-transform: scale(1.1); transform: scale(1.1); }
  100% { -webkit-transform: scale(1); transform: scale(1); }
}
@keyframes player-win {
  0% { -webkit-transform: scale(1); transform: scale(1); }
  50% { -webkit-transform: scale(1.1); transform: scale(1.1); }
  100% { -webkit-transform: scale(1); transform: scale(1); }
}



