/* Board */
.board-container {
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  width: 100%;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -webkit-perspective: 300px;
          perspective: 300px;
}
.board {
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  width: 300px;
  min-width: 300px;
  height: 300px;
  position: relative;
  -webkit-animation: scale 0.5s;
          animation: scale 0.5s;
}
.board__drawing {
  position: absolute;
  top: 0;
  left: 0;
  width: 300px;
  height: 300px;
  pointer-events: none;
}
.board svg {
  fill: #251919;
}

/* Square */
.board__square {
  background: transparent;
  width: 100px;
  height: 100px;
  float: left;
  outline: none;
  cursor: pointer;
}
.board__square svg {
  -webkit-animation: scale 0.4s;
          animation: scale 0.4s;
}

/* Winner's line */
.board__horizontal-line {
  -webkit-animation: horizontal-line 1s;
          animation: horizontal-line 1s;
  fill: #e9851e;
}
.board__vertical-line {
  -webkit-animation: vertical-line 1s;
          animation: vertical-line 1s;
  fill: #e9851e;
}
.board__diagonal-line {
  -webkit-animation: diagonal-line 1s;
          animation: diagonal-line 1s;
  fill: #e9851e;
}
@-webkit-keyframes scale {
  from { -webkit-transform: scale(0.9); transform: scale(0.9); }
  to { -webkit-transform: scale(1); transform: scale(1); }
}
@keyframes scale {
  from { -webkit-transform: scale(0.9); transform: scale(0.9); }
  to { -webkit-transform: scale(1); transform: scale(1); }
}
@-webkit-keyframes horizontal-line {
  from { -webkit-transform: scaleX(0); transform: scaleX(0); }
  to { -webkit-transform: scaleX(1); transform: scaleX(1); }
}
@keyframes horizontal-line {
  from { -webkit-transform: scaleX(0); transform: scaleX(0); }
  to { -webkit-transform: scaleX(1); transform: scaleX(1); }
}
@-webkit-keyframes vertical-line {
  from { -webkit-transform: scaleY(0); transform: scaleY(0); }
  to { -webkit-transform: scaleY(1); transform: scaleY(1); }
}
@keyframes vertical-line {
  from { -webkit-transform: scaleY(0); transform: scaleY(0); }
  to { -webkit-transform: scaleY(1); transform: scaleY(1); }
}
@-webkit-keyframes diagonal-line {
  from { width: 0; }
  to { width: 396px }
}
@keyframes diagonal-line {
  from { width: 0; }
  to { width: 396px }
}



